.switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 20px;
    margin: 5px 0 0 5px;
}
.input{
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #c8e6c0;
    transition: .4s;
    border-radius: 34px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 10px;
    width: 10px;
    left: 4px;
    bottom: 5px;
    background-color: #e00707;
    transition: .4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: #165f77;
}
input:checked + .slider:before {
    transform: translateX(32px);
}
.themeWrap{
    display: flex;
    justify-content: space-between;
}
.h2{
    color: var(--h2);
}
.theme{
    display: flex;

}
