*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html[data-theme='light']{
    --background: #e4e4e5;
    --bgBtn: #cecaca;
    --inp: #d8d3d3;
    --color: #1b1b15;
    --h2: #222126;
    --shad:3px 3px rgb(20, 23, 28);
    --activBg: #c3bbbb;
    --activeShad:2px 2px #14171CFF;
    --borderBtn:1.75px solid black;
}
html[data-theme='dark']{
    --background: #394664;
    --bgBtn: #182035;
    --inp: #182035;
    --color: #e2ee01;
    --h2: #e2ee01;
    --shad:0px 2px rgb(161, 5, 5);
    --activBg: #3e578e;
    --activeShad:0px 3px #bc0e0e;
}