*{
    background-color: var(--background);
    font-family: 'Luckiest Guy', cursive;
}
.wrapper{
    width: 400px;
    margin: 0 auto;
}
.outPut{
    width: 400px;
    height: 70px;
    border-radius: 10px;
    margin:10px 0;
    font-size: 20px;
    background: var(--inp);
    color: var(--color);
    display: flex;
    font-weight: bold;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
    padding: .75rem;
    word-wrap: break-word;
    word-break: break-all;
}

.outPut > div{
    background: var(--inp);
}

.previousOperand {
    font-size: 15px;
}
.body{
    background: var(--bgBtn);
    border-radius: 10px;
    padding: 15px;
    display: flex;
    margin: 0;
    flex-wrap: wrap;
}
.btn{
    width: 70px;
    height: 50px;
    background: var(--background);
    border: var(--borderBtn);
    border-radius: 30px;
    color: var(--color);
    cursor: pointer;
    margin: 15px 10px ;
    box-shadow: var(--shad);
    font-size: 18px;
    font-weight: bold;
}
.btn:active {
    background-color: var(--activBg);
    box-shadow: var(--activeShad);
    transform: translateY(4px);
}
.reset{
    width: 160px;

}
.equal{
    width: 160px;

}
